.App::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  background-image: url("./../access/image/stars.png"); }

.App::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  z-index: -2;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  background-image: url("./../access/image/stars.png"); }

.App::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  background-color: transparent;
  background-image: url("./../access/image/twinkling.png");
  animation: twikling-move 100s infinite linear; }

@keyframes twikling-move {
  from {
    background-position: 0 0; }
  to {
    background-position: 5000px 5000px; } }

.home-page {
  background: linear-gradient(#47cbfb, #fff);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  transform: scale(0.1) translateX(-165vw);
  transition: 2s all;
  transition-property: z-index, transform;
  z-index: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  line-height: 1.5; }
  .home-page.active {
    z-index: 100;
    transform: scale(1) translateX(0); }
  .home-page a {
    padding: 20px; }
  .home-page .cloud {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 307px;
    background: url(./../access/image/cloud.png);
    animation: cloud-move 15s infinite linear; }
  .home-page .ground {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 113px;
    background: url(./../access/image/ground.jpg); }
  .home-page .grass {
    position: absolute;
    bottom: 108px;
    left: 0;
    width: 100%;
    height: 58px;
    background: url(./../access/image/grass.png);
    animation: grass 0.5s infinite steps(3); }
  .home-page .top {
    position: relative; }

@keyframes cloud-move {
  from {
    background-position-x: 0; }
  to {
    background-position-x: 752px; } }

@keyframes grass {
  from {
    background-position-y: 0; }
  to {
    background-position-y: 165px; } }

.skill-page {
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  transform: scale(0.1) translateX(-55vw);
  transition: 2s all;
  transition-property: z-index, transform;
  z-index: 0;
  overflow: auto; }
  .skill-page.active {
    z-index: 100;
    transform: scale(1) translateX(0); }
  .skill-page .roof, .skill-page .ground {
    width: 100%;
    position: absolute;
    left: 0;
    height: 80px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2em;
    text-shadow: 6px 2px #ff0000; }
  .skill-page .container {
    width: 100%;
    height: 100%;
    position: relative;
    min-width: 1024px; }
  .skill-page .roof {
    top: 0;
    background: url(./../access/image/brick-1.png); }
  .skill-page .ground {
    bottom: 0;
    background: url(./../access/image/brick-2.png); }
  .skill-page .animation-container {
    width: 1024px;
    position: absolute;
    top: 80px;
    left: calc(50% - 512px);
    height: calc(100% - 160px); }
  .skill-page .brick-list {
    width: 80%;
    display: flex;
    justify-content: space-around;
    position: absolute;
    bottom: 220px;
    left: 10%; }
    .skill-page .brick-list .brick {
      position: relative;
      width: 80px;
      height: 80px;
      background: url(./../access/image/brick-1.png); }
      .skill-page .brick-list .brick .tree {
        content: "";
        position: absolute;
        left: 0;
        bottom: 100%;
        background: url(./../access/image/mario-tree.png);
        background-size: 100%;
        width: 100%;
        overflow: hidden; }
        .skill-page .brick-list .brick .tree [data-tooltipped] {
          position: absolute;
          left: 10px; }
        .skill-page .brick-list .brick .tree img {
          width: 60px;
          border-radius: 8px;
          cursor: pointer; }
  .skill-page .character {
    width: 64px;
    height: 64px;
    position: absolute;
    bottom: 0; }
    .skill-page .character > span {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(./../access/image/mario.png); }
  .skill-page .animation-container.active .brick {
    animation: brick-raise 0.2s linear; }
    .skill-page .animation-container.active .brick .tree {
      animation: grow-up 2s linear forwards; }
  .skill-page .animation-container.active .brick:nth-child(1) {
    animation-delay: 1s; }
    .skill-page .animation-container.active .brick:nth-child(1) .tree {
      animation-delay: 1.2s; }
  .skill-page .animation-container.active .brick:nth-child(2) {
    animation-delay: 2s; }
    .skill-page .animation-container.active .brick:nth-child(2) .tree {
      animation-delay: 2.2s; }
  .skill-page .animation-container.active .brick:nth-child(3) {
    animation-delay: 3s; }
    .skill-page .animation-container.active .brick:nth-child(3) .tree {
      animation-delay: 3.2s; }
  .skill-page .animation-container.active .brick:nth-child(4) {
    animation-delay: 4s; }
    .skill-page .animation-container.active .brick:nth-child(4) .tree {
      animation-delay: 4.2s; }
  .skill-page .animation-container.active .brick:nth-child(5) {
    animation-delay: 5s; }
    .skill-page .animation-container.active .brick:nth-child(5) .tree {
      animation-delay: 5.2s; }
  .skill-page .animation-container.active .brick:nth-child(6) {
    animation-delay: 6s; }
    .skill-page .animation-container.active .brick:nth-child(6) .tree {
      animation-delay: 6.2s; }
  .skill-page .animation-container.active .brick:nth-child(7) {
    animation-delay: 7s; }
    .skill-page .animation-container.active .brick:nth-child(7) .tree {
      animation-delay: 7.2s; }
  .skill-page .animation-container.active .brick:nth-child(8) {
    animation-delay: 8s; }
    .skill-page .animation-container.active .brick:nth-child(8) .tree {
      animation-delay: 8.2s; }
  .skill-page .animation-container.active .brick:nth-child(9) {
    animation-delay: 9s; }
    .skill-page .animation-container.active .brick:nth-child(9) .tree {
      animation-delay: 9.2s; }
  .skill-page .animation-container.active .brick:nth-child(10) {
    animation-delay: 10s; }
    .skill-page .animation-container.active .brick:nth-child(10) .tree {
      animation-delay: 10.2s; }
  .skill-page .animation-container.active .character {
    animation: character-move 7s forwards linear; }
    .skill-page .animation-container.active .character > span {
      animation: mario-sprite 0.5s steps(3);
      animation-iteration-count: 14; }

@keyframes brick-raise {
  0% {
    transform: translateY(0px); }
  50% {
    transform: translateY(-20px); }
  100% {
    transform: translateY(0px); } }

@keyframes character-move {
  0% {
    left: 0; }
  13%, 18%, 26%, 32%, 40%, 46%, 55%, 61%, 68%, 74%, 82%, 88% {
    bottom: 0; }
  15%, 29%, 43%, 58%, 71%, 85% {
    bottom: 200px; }
  100% {
    left: calc(100% - 64px); } }

@keyframes mario-sprite {
  0% {
    background-position-x: 64px; }
  100% {
    background-position-x: 256px; } }

@keyframes grow-up {
  0% {
    height: 0; }
  100% {
    height: 400px; } }

