body {
  font-family: 'Press Start 2P', cursive; }

.close {
  position: absolute;
  top: 0;
  right: 0;
  font-family: Arial, Helvetica, sans-serif;
  padding: 10px;
  font-size: 3em;
  opacity: 0.8;
  line-height: 0.7;
  cursor: pointer;
  filter: drop-shadow(2px 0 0 #fff) drop-shadow(-2px 0 0 #fff) drop-shadow(0 2px 0 #fff) drop-shadow(0 -2px 0 #fff); }
  .close:hover {
    opacity: 1; }
